@import url(https://fonts.googleapis.com/css2?family=Kanit:wght@100&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Cambay&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Saira:wght@600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Black+Ops+One&display=swap);
body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}
/*
IMPORTANT NOTES 
The header's height needs to be set as the value for the interface div's padding-top
THe footer's height needs to be set as the value for the interface div's padding-bottom

z-index order of elements
.bg-home   1    1
.about     10   5000
.bg-header 20   8000
.bg-footer 120  9000

*/


@-webkit-keyframes page-fade {
  0% { opacity: 0; }
  100% { opacity: 1; }
}


@keyframes page-fade {
  0% { opacity: 0; }
  100% { opacity: 1; }
}

html {
    margin: 0px;
    padding: 0px;
    scroll-behavior: smooth;
    background-color: #000;
    position: relative;
    font-family: 'Saira', 'Cambay', 'Kanit', 'Segoe UI', Helvetica;
}

::-webkit-scrollbar {
  display: none;
}

.post-body pre code::-webkit-scrollbar {
  display: block;
}

body a, p, ul, li {
    margin: 0px;
    padding: 0px;
    word-wrap: break-word;
    font-weight: normal;
    line-height: 1em;
    text-decoration: none;
}

div { margin: 0px; padding: 0px; }

.container {
  width: 100vw;
  height: 100vh;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr;
}





/* BACKGROUND */

.bg-home {
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100vh;
  border: 0px;
  z-index: 1;
  opacity: 1;
  position: fixed;
  background-size: auto 120vh;
  background-repeat: repeat-x;
  background-position: -450vw 0vh;
  background-image: url("https://github.com/datamesse/datamesse.github.io/blob/main/src/assets-theme/background-panorama-beach.png?raw=true");
}

.bg-home-dots {
  width: auto;
  height: 100vh;
  opacity: 1;
  background-image: url("https://github.com/datamesse/datamesse.github.io/raw/main/src/assets-theme/background-square-main.svg");
}

.bg-blue {
  top: 0px;
  left: 0px;
  z-index: 1;
  width: 100%;
  height: 100vh;
  border: 0px;
  position: fixed;
  scroll-behavior: smooth;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: auto 100vh;
}

.bg-blue-map {
  height: 100vh;
  width: 100%;
  border: 0px;
  z-index: 1;
  opacity: 1;
  background-size: auto 100vh;
  background-repeat: repeat-x;
  background-position: -450vw 0vh;
  background-image: url("https://github.com/datamesse/datamesse.github.io/raw/main/src/assets-theme/background-triangle-map-blue-1289379.svg");
}

.bg-blue-tiles {
  width: auto;
  height: 100vh;
  opacity: 0.375;
  background-image: url("https://raw.githubusercontent.com/datamesse/datamesse.github.io/main/src/assets-theme/background-tile-blue-transparent.svg");
}

.gear-blue-1 {
  opacity: 0.85;
  background-repeat: no-repeat;
  background-image: url("https://github.com/datamesse/datamesse.github.io/raw/main/src/assets-theme/background-gear-blue-7147602.svg");
  position: absolute;
}

.gear-1-spin {
  -webkit-animation-name: gear-1-spinning;
          animation-name: gear-1-spinning;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

@-webkit-keyframes gear-1-spinning {
  from { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
  to { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
}

@keyframes gear-1-spinning {
  from { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
  to { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
}

.gear-blue-2 {
  opacity: 0.85;
  background-repeat: no-repeat;
  background-image: url("https://github.com/datamesse/datamesse.github.io/raw/main/src/assets-theme/background-gear-blue-7153617.svg");
  position: absolute;
}

.gear-2-spin {
  -webkit-animation-name: gear-2-spinning;
          animation-name: gear-2-spinning;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

@-webkit-keyframes gear-2-spinning {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes gear-2-spinning {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.bg-beige {
  top: 0px;
  left: 0px;
  z-index: 1;
  width: 100%;
  height: 100%;
  position: fixed;
  background: #F7F7F7;
  scroll-behavior: smooth;
  background-repeat: no-repeat;
  background-attachment: fixed;
}

.bg-beige-tiles {
  height: 100vh;
  width: auto;
  border: 0px;
  opacity: 1;
  background-image: url("https://raw.githubusercontent.com/datamesse/datamesse.github.io/main/src/assets-theme/background-tile-beige-grey.svg");
}

.gear-beige-1 {
  opacity: 1;
  background-repeat: no-repeat;
  background-image: url("https://github.com/datamesse/datamesse.github.io/raw/main/src/assets-theme/background-gear-beige-7147602.svg");
  position: absolute;
}

.gear-beige-2 {
  opacity: 1;
  background-repeat: no-repeat;
  background-image: url("https://github.com/datamesse/datamesse.github.io/raw/main/src/assets-theme/background-gear-beige-7153617.svg");
  position: absolute;
}

@-webkit-keyframes bg-auto-scrolling {
  100% {
    background-position: 3000vw 0vh;
  }
}

@keyframes bg-auto-scrolling {
  100% {
    background-position: 3000vw 0vh;
  }
}

.interface {
  overflow-y: auto;
  padding-top: 6.5vh;
  left: 0px;
  height: -webkit-max-content;
  height: max-content;
  grid-row-start: 1;
  grid-row-end: 2;
}





/* PAGES */

.page {
  -webkit-animation: page-fade 2.5s;
          animation: page-fade 2.5s;
}

.about {
  width: inherit;
  position: relative;
  z-index: 5000;
  top: 0px;
}

.about-panel {
  width: 100%;
  display: grid;
  position: relative; /* only used to put about-triangle at the bottom */
}

.about-background-top {
  box-sizing: border-box;
  border: 1px solid #8A8A86;
  border-top: none;
  grid-row-start: 1;
  grid-row-end: 3;
  grid-column-start: 1;
  grid-column-end: 11;
  background: #F7F7F7;
  background-image: url("https://raw.githubusercontent.com/datamesse/datamesse.github.io/main/src/assets-theme/background-tile-beige-grey.svg"); 
  z-index: 5001;
}

.about-background-right {
  box-sizing: border-box;
  border: 1px solid #8A8A86;
  grid-row-start: 8;
  grid-row-end: 12;
  grid-column-start: 6;
  grid-column-end: 11;
  background: #F7F7F7;
  background-image: url("https://raw.githubusercontent.com/datamesse/datamesse.github.io/main/src/assets-theme/background-tile-beige-grey.svg"); 
  z-index: 5001;
}

.about-blue-dash {
  width: inherit;
  margin: auto 0px auto 0px;
  background-color: #3B54CD;
  grid-row-start: 1;
  grid-row-end: 2;
  grid-column-start: 1;
  grid-column-end: 2;
  z-index: 5002;
}

.about-name {
  color: #3F3F3F;
  font-family: 'Cambay', 'Kanit', 'Segoe UI', Helvetica;
  grid-row-start: 1;
  grid-row-end: 2;
  grid-column-start: 2;
  grid-column-end: 5;
  display: flex;
  justify-content: start;
  align-items: center;
  z-index: 5002;
}

.about-subtext-container {
  color: #CBCCC9;
  line-height: 1;
  font-family: 'Saira', 'Cambay', 'Kanit', 'Segoe UI', Helvetica;
  align-items: center;
  display: grid;
  grid-template-rows: 80% 20%;
  z-index: 5002;
}

.about-subtext-photo-position {
  grid-row-start: 2;
  grid-row-end: 3;
  grid-column-start: 2;
  grid-column-end: 5;
}

.about-subtext-status-position {
  grid-row-start: 2;
  grid-row-end: 3;
  grid-column-start: 6;
  grid-column-end: 9;
}

.about-rectangle {
  width: inherit;
  height: 50%;
  box-sizing: border-box;
  border: 2px solid rgba(205, 207, 203, 0.9);
  background-color: rgba(212, 214, 210, 0.7);
  grid-row-start: 1;
  grid-row-end: 2;
  grid-column-start: 1;
  grid-column-end: 2;
}

.about-subtext {
  grid-row-start: 1;
  grid-row-end: 2;
  grid-column-start: 2;
  grid-column-end: 5;
}

.about-subtext-container-vertical {
  color: #CBCCC9;
  line-height: 1;
  font-family: 'Saira', 'Cambay', 'Kanit', 'Segoe UI', Helvetica;
  display: flex;
  justify-content: center;
  display: grid;
  width: inherit;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  grid-template-columns: 20% 80% ;
}

.about-subtext-gear-position {
  grid-row-start: 9;
  grid-row-end: 10;
  grid-column-start: 10;
  grid-column-end: 11;
  z-index: 5002;
}

.about-subtext-container-vertical .about-rectangle {
  margin: 0px;
  height: 100%;
  width: 50%;
  grid-row-start: 1;
  grid-row-end: 2;
  grid-column-start: 2;
  grid-column-end: 5;
}

.about-subtext-container-vertical .about-subtext {
  -webkit-writing-mode: vertical-rl;
      -ms-writing-mode: tb-rl;
          writing-mode: vertical-rl;
  -webkit-text-orientation: mixed;
          text-orientation: mixed;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  top: 0px;
  left: 0px;
  grid-row-start: 2;
  grid-row-end: 3;
  grid-column-start: 2;
  grid-column-end: 5;
}

.about-lanyard-clip {
  margin-bottom: -3%;
  background-color: rgba(242, 243, 241, 0.75);
  border: 1px solid rgba(141, 142, 140, 0.3);
  border-top: none;
  border-radius: 0 0 7.5% 7.5%;
  box-shadow: inset 0 0 5px #DADBDA;
  grid-row-start: 3;
  grid-row-end: 5;
  grid-column-start: 3;
  grid-column-end: 4;
  z-index: 5003;
}

.about-lanyard-tag {
  grid-row-start: 4;
  grid-row-end: 7;
  grid-column-start: 2;
  grid-column-end: 5;
  background-image: url("https://raw.githubusercontent.com/datamesse/datamesse.github.io/main/src/assets-theme/lanyard-tag.svg?raw=true");
  background-repeat: no-repeat;
  background-size: contain;
  z-index: 5002;
}

.about-lanyard-text {
  display: flex; 
  justify-content: center;
  align-items: flex-end;
  line-height: 1;
  background: linear-gradient(to bottom, #545453, #DADCDB);
  -webkit-background-clip: text;
  color: transparent;
  grid-row-start: 4;
  grid-row-end: 6;
  grid-column-start: 2;
  grid-column-end: 3;
  z-index: 5003;
}

.about-avatar {
  display: flex;
  justify-content: center;
  overflow: hidden;
  grid-row-start: 7;
  grid-row-end: 10;
  grid-column-start: 2;
  grid-column-end: 5;
  z-index: 5002;
}

.about-avatar img {
  height: 100%;
  box-sizing: border-box;
}

.about-avatar-shadow {
  margin-top: 5px;
  margin-left: 15px;
  z-index: 5001;
}

.about-avatar-shadow img {
  -webkit-filter: brightness(0) saturate(0) sepia(1);
          filter: brightness(0) saturate(0) sepia(1);
  opacity: 0.4;
}

.about-avatar-border {
  box-sizing: border-box;
  border: 2px solid rgba(81, 82, 80, 0.6);
  border-top: none;
  box-shadow: inset 0 0 10px rgba(71, 72, 70, 0.7);
  z-index: 5005;
  grid-row-start: 7;
  grid-row-end: 10;
  grid-column-start: 2;
  grid-column-end: 5;
}

.about-avatar-highlight {
  margin: 8px;
  height: 12%;
  background: linear-gradient(180deg, rgba(255, 255, 255, 0.4) 0%, rgba(255, 255, 255, 0.3) 5%, rgba(255, 255, 255, 0.2) 25%, rgba(255, 255, 255,0) 100%);
  z-index: 5005;
  grid-row-start: 7;
  grid-row-end: 10;
  grid-column-start: 2;
  grid-column-end: 5; 
}

.about-scroll-triangle {
  right: 0px;
  bottom: 0px;
  color: #4F4F50;
  font-family: 'Saira', 'Cambay', 'Kanit', 'Segoe UI', Helvetica;
  line-height: 1;
  grid-row-start: 3;
  grid-row-end: 8;
  grid-column-start: 9;
  grid-column-end: 10;
