  z-index: 5003;
}

.about-description {
  color: #4F4F50;
  font-family: 'Saira', 'Cambay', 'Kanit', 'Segoe UI', Helvetica;
  box-sizing: border-box;
  grid-row-start: 3;
  grid-row-end: 8;
  grid-column-start: 6;
  grid-column-end: 10;
