}

.about-description-darken {
  background: rgba(215, 215, 222, 0.55);
}

.about-description p {
  padding: 0px 0px 0px 0.5vh;
  line-height: 2;
}

.about-description ul {
  padding: 0px;
}

.about-description ul li {
  list-style-type: none;
  padding: 0px 0px 0px 0.5vh;
  line-height: 2;
}

.links {
  grid-row-start: 9;
  grid-row-end: 10;
  grid-column-start: 6;
  grid-column-end: 10;
