  top: 0.5vh;
  right: 0px;
  height: 3.01vh;
  overflow: hidden;
  box-sizing: border-box;
  justify-content: space-between;
  position: absolute;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1.7vh 1.3vh 0.01vh;
  -webkit-clip-path: polygon(100% 0, 100% 100%, 1.301vh 100%, 0 calc(100% - 1.301vh), 0 0);
          clip-path: polygon(100% 0, 100% 100%, 1.301vh 100%, 0 calc(100% - 1.301vh), 0 0);
}

.mp3-bars {
  width: 100%;
  height: 100%;
  display: flex;
  grid-row-start: 1;
  grid-row-end: 2;
  grid-column-start: 1;
  grid-column-end: 2;
}

.mp3-bars span {
  padding: 0px;
  content: '';
  background: linear-gradient(to top, #FDFEFC 10%, #CFD0CE 80%);
  -webkit-transform-origin: bottom;
          transform-origin: bottom;
  -webkit-animation: mp3-bounce 7.2s ease infinite alternate;
          animation: mp3-bounce 7.2s ease infinite alternate;
  &:nth-of-type(2) { -webkit-animation-delay: -0.5s; animation-delay: -0.5s; }
  &:nth-of-type(3) { -webkit-animation-delay: -1.2s; animation-delay: -1.2s; }
  &:nth-of-type(4) { -webkit-animation-delay: -1.6s; animation-delay: -1.6s; }
  &:nth-of-type(5) { -webkit-animation-delay: -2.1s; animation-delay: -2.1s; }
  &:nth-of-type(6) { -webkit-animation-delay: -2.4s; animation-delay: -2.4s; }
  &:nth-of-type(7) { -webkit-animation-delay: -2.7s; animation-delay: -2.7s; }
  &:nth-of-type(8) { -webkit-animation-delay: -3.3s; animation-delay: -3.3s; }
  &:nth-of-type(9) { -webkit-animation-delay: -3.8s; animation-delay: -3.8s; }
  &:nth-of-type(10) { -webkit-animation-delay: -4.2s; animation-delay: -4.2s; }
  &:nth-of-type(11) { -webkit-animation-delay: -4.6s; animation-delay: -4.6s; }
}

@-webkit-keyframes mp3-bounce {
  10% { -webkit-transform: scaleY(0.1); transform: scaleY(0.1); }
  30% { -webkit-transform: scaleY(0.8); transform: scaleY(0.8); }
  50% { -webkit-transform: scaleY(0.3); transform: scaleY(0.3); }
  70% { -webkit-transform: scaleY(0.65); transform: scaleY(0.65); }
  90% { -webkit-transform: scaleY(0.4); transform: scaleY(0.4); }
}

@keyframes mp3-bounce {
  10% { -webkit-transform: scaleY(0.1); transform: scaleY(0.1); }
  30% { -webkit-transform: scaleY(0.8); transform: scaleY(0.8); }
  50% { -webkit-transform: scaleY(0.3); transform: scaleY(0.3); }
  70% { -webkit-transform: scaleY(0.65); transform: scaleY(0.65); }
  90% { -webkit-transform: scaleY(0.4); transform: scaleY(0.4); }
}

.mp3-player p {
