    margin: 0px 1.75vh 0.7vh 0px;
  }
  
  .post-preview-link a {
    font-size: 1.4vh;
  }
  
  .post {
    margin: 4.9vh 0px 0px 0px;
  }
  
  .post-panel {
    background-size: 4vh 4vh;
    grid-template-rows: 1.4vh 8vh auto;
    grid-template-columns: 1.4vh auto 1.4vh;
  }
  
  .post-title-banner {
    -webkit-clip-path: polygon(100% 0, 100% calc(100% - 0.7vh), calc(100% - 0.7vh) 100%, 0 100%, 0 0);
            clip-path: polygon(100% 0, 100% calc(100% - 0.7vh), calc(100% - 0.7vh) 100%, 0 100%, 0 0);
  }
  
  .post-panel .separator-glow {
    height: 4.5vh;
  }
  
  .post-title {
    font-size: 2.1vh;
    letter-spacing: 0.14vh;
  }
  
  .post-title p {
    margin: 0px 0px 0px 0.7h;
  }
  
  .post-date {
    margin: 2.1vh 0px 0px 0px;
    font-size: 1.75vh;
  }
  
  .post-body {
    margin: 6.3vh 0px 0px 0px;
    width: calc(40vw - 2.8vh);
    font-size: 2.1vh;
  }
  
  .post-body pre {
    margin: 0px 0px 1.4vh 0px;
    padding: 1.4vh;
    font-size: 1.54vh;
    -webkit-clip-path: polygon(100% 0, 100% calc(100% - 1.4vh), calc(100% - 1.4vh) 100%, 0 100%, 0 0);
            clip-path: polygon(100% 0, 100% calc(100% - 1.4vh), calc(100% - 1.4vh) 100%, 0 100%, 0 0);
  }
  
  .post-body p {
    margin: 0px 0px 1.4vh 0px;
  }
  
  .post-body ul {
    margin-left: 2.8vh;
  }
  
  .post-body ul li {
    margin: 0px 0px 0.7vh 0px;
  }
  
  .post-body ol li {
    margin: 0px 0px 0.7vh 0px;
  }
  
  .post-body img {
    width: calc(40vw - 2.8vh);
  }
  
  .embedded-excel {
    width: calc(40vw - 2.8vh);
    height: 1070px;
  }

  .not-found {
    margin: 5vw 0px 0px 0px;
    grid-template-rows: 1vw 4vw auto 4vw 1vw;
    grid-template-columns: 1vw 4vw auto 4vw 1vw;
  }
  
  .not-found p {
    padding: 2vw;
    font-size: 1vw;
  }

  .mp3-player {
    width: 4vw;
  }
  
  .mp3-bars span {
    margin: 0px 0px 0px 0.18vw;
    width: 0.2vw;
    border-radius: 0 0 0.1vw 0.1vw;
  }

  .separator {
    grid-template-columns: 2.25vh 22.5vh auto;
  }

  .separator-glow {
    width: 0.2625vh;
  }

  .menu-container {
    grid-template-rows: 6.6666vw auto;
  }

  .menu-button-border {
    height: 5.8vw;
    width: 5.8vw;
    box-sizing: border-box;
    border: 0.25vw solid #EAF1F5;
    box-shadow: 0px 0px 4px 1px rgba(0, 0, 0, 0.65);
  }
  
  .menu-button-highlight {
    height: 5vw;
    width: 5vw;
  }
  
  .menu-button-gradient {
    height: 4.7vw;
    width: 4.7vw;
  }
  
  .menu-button-icon {
    width: 3.25vw;
    height: 3.25vw;
  }

  .menu-label-light {
    margin: 0.0005vw auto 0.0005vw auto;
    height: 0.2vw;
  }
  
  .menu-labels-container p {
    padding: 0.3vw 0px 0.2025vw 0px;
    font-size: 0.85vw;
  }

}
